import { Suspense } from 'react';
import PropertyDetailsClient from './PropertyDetailsClient';
import connectDB from '@/lib/db';
import Property from '@/models/property';
import Enquiry from '@/models/enquiry';

async function getPropertyWithEnquiries(id) {
  try {
    await connectDB();
    
    const [property, enquiriesCount] = await Promise.all([
      Property.findById(id).populate('owner', 'name email phone'),
      Enquiry.countDocuments({ property: id })
    ]);
    
    if (!property) {
      throw new Error('Property not found');
    }

    const propertyObj = JSON.parse(JSON.stringify(property));
    return {
      ...propertyObj,
      enquiriesCount
    };
  } catch (error) {
    console.error('Error fetching property:', error);
    throw error;
  }
}

export default async function PropertyDetailsPage({ params: { id } }) {
  try {
    const property = await getPropertyWithEnquiries(id);
    return (
      <Suspense fallback={<div className="container mx-auto px-4 py-8 text-center">Loading property details...</div>}>
        <PropertyDetailsClient initialProperty={property} />
      </Suspense>
    );
  } catch (error) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="text-center text-red-500">
          {error.message || 'Error loading property'}
        </div>
      </div>
    );
  }
}
